___CSS_LOADER_EXPORT___.push([module.id, "\n/* Prefer portrait mode for very small screen sizes */\n@media only screen and ((orientation: portrait) or (max-width: 620px)) {\n    #fp-main {\n        flex-direction: column;\n    }\n\n    #fp-map {\n        max-height: 50%;\n    }\n\n    #fp-summary {\n        height: 50%;\n        width: 100%;\n        border-left: 0px;\n        border-top: 2px solid black;\n    }\n\n    .tab-container {\n        grid-template-columns: auto auto;\n        grid-template-rows: min-content auto;\n    }\n\n    .tab-content {\n        grid-column: span 2;\n        grid-row: 2;\n    }\n\n    .tab-container > label {\n        /* height: 2.5em; */\n        margin-top: 0;\n        margin-right: .25em;\n        margin-left: .25em;\n        margin-bottom: 0;\n        writing-mode: horizontal-tb;\n        /* text-orientation: mixed; */\n    }\n\n    .tab-container > input:hover + label:before {\n        top    : auto;\n        left    : 20%;\n        bottom  : 0;\n        height  : 0.4em;\n        width   : 60%;\n        border-bottom: 0.2em solid var(--theme-highlight);\n        border-bottom-style: inset;\n        border-left-style: none;\n    }\n\n    .tab-container > input:checked + label:before {\n        top    : auto;\n        left    : 20%;\n        bottom  : 0;\n        height  : 0.4em;\n        width   : 60%;\n        border-bottom: 0.2em solid var(--theme-primary);\n        border-bottom-style: inset;\n        border-left-style: none;\n    }\n}\n\n@media only screen and (max-width: 50em) {\n    .fp-long-name {\n        display: none;\n    }\n\n    .leaflet-control-attribution {\n        display: none;\n    }\n}\n\n@media (max-height: 40em) or (max-width: 50em) {\n    .leaflet-control-attribution {\n        display: none;\n    }\n\n    #fp-summary > * {\n        font-size: x-small;\n        /* width: 25em; */\n    }\n\n    .mission-features-dragdrop-content {\n        /* flex: auto; */\n        font-size:medium;\n        padding: 0.25em;\n    }\n\n    .mission-features-dragdrop-content-large {\n        font-size:large;\n    }\n\n    /* .mission-feature-content-value {\n        font-size: x-small;\n    }\n\n    .fp-stats-content-value {\n        font-size: x-small;\n    } */\n\n    .mission-feature-title-button {\n        display: none;\n    }\n\n    .mission-feature-title-button-small {\n        display: block;\n    }\n}\n\n@media only screen and (orientation: portrait) and (max-height: 400px) {\n    #fp-summary {\n        height: 100%;\n        width: 100%;\n    }\n}\n\n@media only screen and (orientation: landscape) and (max-height: 400px) and (max-width: 620px) {\n    #fp-map {\n        max-height: 100%;\n    }\n\n    #fp-summary {\n        display: none;\n    }\n}\n\n@media print {\n    @page {\n        size: A4;\n        margin: 10mm;\n    }\n\n    /* body > * {\n        display: none;\n    } */\n    html {\n        /* height: auto; */\n        /*\n        Manually set our height and width\n        to make sure that we can scale the map\n        properly for display later.\n        Also account for page margin and a slight inset to make sure there's no unneeded overflow.\n        */\n        width: 190mm;   /* 210 */\n        height: 276mm;   /* 297 */\n    }\n\n    body {\n        height: auto;\n        width: auto;\n    }\n\n    #app {\n        height: auto;\n        width: auto;\n        display: block;\n        overflow-x: auto;\n    }\n\n    #fp-main {\n        overflow: initial;\n    }\n    #fp-manage {\n        display: none;\n    }\n    #fp-summary {\n        display: none;\n    }\n    #fp-draw {\n        display: none;\n    }\n    #fp-map {\n        width: calc(190mm);\n        height: calc(277mm/2);\n    }\n    #fp-map-interactive {\n        display: block;\n    }\n    #fp-map-help {\n        display: none;\n    }\n    #print-section {\n        display: block;\n    }\n\n    .leaflet-control-attribution {\n        display: none;\n    }\n}\n", "",{"version":3,"sources":["webpack://./s